export default {
	data() {
		return {
			historyList:[]
		};
	},
	onLoad(){
		this.getHistoryInfo();
	},
	methods:{
		async getHistoryInfo(){
			const res = await this.$myRuquest({
				url:'/history/today?'+this.myKey+'&type=1'
			})
			console.log(res);
			this.historyList=res.data.data;
		}
	}
}
